import axios from 'axios';
export default {
  data() {
    return {
      // 最大个签文字数
      maxlength: 300,
      // 性别选择(name 1男 2女)
      genderChecked: 0,
      // 个签内容
      signatureContent: '',
      // 个签剩余可输入长度
      leftover: 300,
      // 当前登录用户的信息
      userInfo: {},
      // 用户昵称
      userNickname: '',
      // 用户头像
      userHeadUrl: ''
    };
  },

  mounted() {
    // console.log(JSON.parse(sessionStorage.getItem('userInfo')));
    this.userInfo = JSON.parse(sessionStorage.getItem('userInfo')); // console.log(this.userInfo);

    this.userNickname = this.userInfo.nickname;
    this.signatureContent = this.userInfo.signature;
    this.genderChecked = this.userInfo.gender;
    this.userHeadUrl = this.userInfo.avatarUrl; // console.log(this.userNickname, this.signatureContent, this.genderChecked);
  },

  methods: {
    // 性别更改
    genderChange() {// console.log('当前性别：', this.genderChecked);
    },

    // 头像上传
    handleFileUpload(event) {
      // console.log(event.target.files[0]);
      var file = event.target.files[0]; // console.log('file', file);
      // 上传头像

      this.uploadHead(file);
    },

    // 上传头像(一天最多修改两次)，参考 https://github.com/Binaryify/NeteaseCloudMusicApi/blob/master/public/avatar_update.html
    async uploadHead(file) {
      // 创建一个 FormData 对象，是 JavaScript内置对象，用于创建表单数据。可在发送 AJAX 请求时，以一种简单且灵活的方式构建和处理表单数据。
      var formData = new FormData(); // 将选中的图片文件添加到 FormData 中

      formData.append('imgFile', file); // console.log('formData', formData);
      // 获取图片尺寸，Promise 形式，需要 await 

      const imgSize = await this.getImgSize(file); // const imgSize = this.getImgSize(file)
      // console.log('图片尺寸', imgSize);

      const cookie = sessionStorage.getItem("cookie");
      const timestamp = new Date().getTime(); // 上传头像

      const res = await axios({
        baseURL: 'http://localhost:3000/',
        method: 'post',
        url: `/avatar/upload?cookie=${cookie}&imgSize=${imgSize.width}&imgX=0&imgY=0&timestamp=${timestamp}`,
        headers: {
          'Content-Type': 'multipart/form-data'
        },
        data: formData
      }); // console.log(res);
      // console.log(res.data.data.url);
      // 设置本地内存的用户信息和当前显示用户头像

      this.userInfo.avatarUrl = this.userHeadUrl = res.data.data.url; // console.log('修改头像后的用户信息：', this.userInfo);
      // 重新保存更新后的用户信息

      sessionStorage.setItem('userInfo', JSON.stringify(this.userInfo));
    },

    // 获取图片尺寸
    getImgSize(file) {
      // Promise 形式
      return new Promise((resolve, reject) => {
        // FileReader 是一个 Web API 提供的用于读取文件内容的接口，允许以异步方式读取文件，
        // 并获取文件的数据，可使用 FileReader 来读取用户选择的文件，例如图片、文本等。
        let reader = new FileReader();
        reader.readAsDataURL(file); // 当文件读取完成时触发的事件

        reader.onload = function (theFile) {
          // new Image() 是 JavaScript 中创建图像对象的方法。用于动态创建一个 <img> 元素的实例，可以通过该实例来加载和操作图像。
          let image = new Image();
          image.src = theFile.target.result;

          image.onload = function () {
            resolve({
              width: this.width,
              height: this.height
            });
          };
        };
      });
    },

    // 保存/提交修改后的用户信息
    saveUserInfo() {
      // console.log(this.userNickname, this.signatureContent, this.genderChecked);
      // 修改用户信息
      this.userInfo.nickname = this.userNickname;
      this.userInfo.signature = this.signatureContent;
      var gender = parseInt(this.genderChecked);
      this.userInfo.gender = gender; // console.log('修改后用户信息：', this.userInfo);
      // 重新保存更新后的用户信息

      sessionStorage.setItem('userInfo', JSON.stringify(this.userInfo));
      const cookie = sessionStorage.getItem("cookie");
      const timestamp = new Date().getTime(); // 提交修改

      setTimeout(async () => {
        const res = await axios({
          baseURL: 'http://localhost:3000/',
          method: 'post',
          url: `/user/update?cookie=${cookie}&gender=${gender}&signature=${this.signatureContent}&nickname=${this.userNickname}&timestamp=${timestamp}`,
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }); // console.log(res);

        alert('修改个人资料成功');
      }, 500);
    }

  },
  watch: {
    // 个签剩余可输入长度随着输入内容增多而减短
    signatureContent() {
      this.leftover = 300 - this.signatureContent.length;
    }

  }
};